





































import Vue from 'vue';

interface IColumnConfig {
    title: string;
    value: string;
    type?: string | undefined;
    editable?: boolean;
    sortable?: boolean;
}

interface ITableItem {
    [key: string]: string | number;
}

interface SearchConfig {
    fields: string[];
    filters: Array<(v: string) => string>;
}

interface ISortColumns {
    [col: string]: number;
}

export default Vue.extend({

    name: 'DataTable',
    props: {
        items: {type: Array as () => ITableItem[]},
        columns: {type: Array as () => IColumnConfig[]},
        search: {type: Object as () => SearchConfig},
    },
    data() {
        return {
            sortStatus: {} as ISortColumns,
            valueSearch: '',
            sortedDown: 'fa-sort-down',
            sortedUp: 'fa-sort-up',
            sortedNot: 'fa-sort',

        };
    },
    created() {
        this.initSort();
    },

    computed: {

        isSearch(): boolean {
            return this.search != null;
        },

        actualItems(): ITableItem[] {
            const filtered = this.items.filter((el: ITableItem) => {
                let isSearch: boolean;
                isSearch = false;
                if (this.search) {
                    this.search.fields.forEach((field: string) => {
                        const nFilters = this.search.filters.length;
                        for (let i = 0; i < nFilters; i++) {
                            const valueS = this.valueSearch;
                            const valueItem: string = "" + el[field];
                            if (!isSearch &&
                                this.search.filters[i](valueItem).includes(this.search.filters[i](valueS))) {
                                isSearch = true;
                            }
                        }
                    });
                } else {
                    isSearch = true;
                }
                return isSearch;
            });
            let actualColumn = '';
            for (const temp in this.sortStatus) {
                if (this.sortStatus[temp] !== 0) {
                    actualColumn = temp;
                }
            }

            if (actualColumn !== '') {
                return filtered.sort((a: ITableItem, b: ITableItem) => {
                        return (typeof a[actualColumn] === 'number')
                            ? this.sortStatus[actualColumn] * (+a[actualColumn] - +b[actualColumn])
                            : this.sortStatus[actualColumn] * (("" + a[actualColumn]).localeCompare("" + b[actualColumn]));
                    },
                );
            } else {
                return filtered;
            }
        },
    },

    methods: {
        initSort(): void {
            for (let temp = 0; temp < this.columns.length; temp++) {
                this.$set(this.sortStatus, this.columns[temp].value, 0);
            }
        },
        typeFormat(type: string): string {
            return (type != "undefined" && type === "number") ? "align-right" : "align-left"
        },
        sortData(col: IColumnConfig): void {
            let newValue: number = this.sortStatus[col.value];
            for (const temp in this.sortStatus) {
                this.$set(this.sortStatus, temp, 0);
            }
            switch (newValue) {
                case 0:
                    newValue = 1;
                    break;
                case 1:
                    newValue = -1;
                    break;
                default :
                    newValue = 0;
            }
            this.$set(this.sortStatus, col.value, newValue);
        },
    },
});
