








import Vue from 'vue';

type Color = 'warning' | 'success' | 'danger' | 'info' | 'primary' | 'red' | 'green' | 'yellow' | 'blue';
type Size = 'small' | 'medium' | 'large';

export default Vue.extend({
    name: 'MyButton',
    props: {
        size: {
            type: String as () => Size,
            default: 'medium',
        },
        color: {type: String as () => Color},
        text: String,
    },
    methods: {
        clickButton() {
            this.$emit('click');
        },
    },
});
