
























import Vue from 'vue';

export default Vue.extend({
    name: 'Modal',
    data() {
        return {
            visible: false,
        };
    },
    methods: {
        show(): void {
            this.visible = true;
            this.$emit('open');
        },
        close(): void {
            this.visible = false;
            this.$emit('close');
        },
    },
});
